/*  NESTABLE */
.dd {
  position: relative;
  display: block;
  margin: 0;
  padding: 0;
  list-style: none;
}
.dd button {
  outline: none;
}

.dd-list {
  display: block;
  position: relative;
  margin: 0;
  padding: 0;
  list-style: none;
}

.dd-list .dd-list {
  padding-left: 2rem;
}

.dd-collapsed .dd-list {
  display: none;
}

.dd-item,
.dd-empty,
.dd-placeholder {
  display: block;
  position: relative;
  margin: 0;
  padding: 0;
  min-height: 2rem;
  line-height: 2rem;
}

.dd-handle {
  display: block;
  height: 2.66rem;
  margin: 0.33rem 0;
  padding: 0.33rem 0.66rem;
  text-decoration: none;
  border: 1px solid #d9dee9;
  background: #fff;
  border-radius: 3px;
  cursor: move;
}
.dd-handle:hover {
  color: #fff;
  background: var(--kit-color-primary);
  border-color: var(--kit-color-primary);
}

.dd-item .dd-item-toggle {
  display: block;
  position: relative;
  cursor: pointer;
  float: left;
  width: 2rem;
  height: 2rem;
  margin: 0.33rem 0;
  margin-left: 30px;
  padding: 0;
  text-indent: 100%;
  white-space: nowrap;
  overflow: hidden;
  border: 0;
  background: transparent;
  line-height: 2.2rem;
  text-align: center;
  font-family: "FontAwesome", sans-serif;
  font-size: 0.73rem;
  color: #595c97;
}
.dd-item .dd-item-toggle:before {
  content: "";
  display: block;
  position: absolute;
  width: 100%;
  text-align: center;
  text-indent: 0;
}
.dd-item .nestable-item-container {
  display: block;
  min-height: auto;
  color: inherit;
  font-size: inherit;
  margin-bottom: 0;
}

.draggable-placeholder-inner,
.dd-empty {
  margin: 0.33rem 0;
  padding: 0;
  min-height: 2rem;
  background: #e4e9f0;
